import React from "react";
// @mui
import { Container, Box, Typography } from "@mui/material";
import { useTheme, alpha } from "@mui/material/styles";
// hooks
import useAuth from "../../hooks/useAuth";
// components
import Page from "../../components/Page";
import useSettings from "../../hooks/useSettings";
import { useTranslation } from "react-i18next";
// sections
import { MotionContainer } from "../../components/animate";

// ----------------------------------------------------------------------

export default function GeneralApp() {
  const { user } = useAuth();
  const { t } = useTranslation();
  const { themeStretch } = useSettings();

  return (
    <Page title="Home: Dashboard">
      <RemesasBg>
        <Container
          component={MotionContainer}
          maxWidth={themeStretch ? false : "xl"}
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
            textAlign: "center",
            minHeight: 250,
          }}
        >
          <Typography variant="h3" sx={{ fontWeight: 700, mb: 1 }}>
            {t("hello_user", {
              name: user?.first_name + " " + user?.last_name,
            })}
          </Typography>

          <Typography variant="subtitle1" sx={{ opacity: 0.75 }}>
            {t("welcome_back")}
          </Typography>
        </Container>
      </RemesasBg>
    </Page>
  );
}

// ----------------------------------------------------------------------

const RemesasBg = ({ children }) => {
  const theme = useTheme();

  const c1 = alpha(theme.palette.primary.light, 0.35);
  const c2 = alpha(theme.palette.info.light || theme.palette.primary.main, 0.28);

  return (
    <Box
      sx={{
        position: "relative",
        overflow: "hidden",
        borderRadius: 3,
        px: { xs: 2, sm: 3 },
        py: { xs: 4, sm: 6 },
        bgcolor: theme.palette.background.default,
        "&::before": {
          content: '""',
          position: "absolute",
          inset: 0,
          background: `
            radial-gradient(circle at 20% 30%, ${c1}, transparent 60%),
            radial-gradient(circle at 80% 70%, ${c2}, transparent 60%)
          `,
          zIndex: 0,
          filter: "blur(18px)",
        },
      }}
    >
      <Box sx={{ position: "relative", zIndex: 1 }}>{children}</Box>
    </Box>
  );
};
